 las inquietudes, se continúan presentando cada una de las prendas de ser posible en modelos para visualizar mejor la apariencia, caída y ajuste, es de anotar que previo a esto se debió realizar una medición para evitar sorpresas en la reunión. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5"},[_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"fade-left"}},[_c('a',{staticClass:"anexo mb-4 mb-lg-0",attrs:{"href":"https://youtu.be/7zJ1t5S2CoY","target":"_blank"}},[_c('div',{staticClass:"anexo__icono"},[_c('img',{attrs:{"src":require("@/assets/template/icono-youtube.svg")}})]),_c('div',{staticClass:"anexo__texto"},[_c('p',[_c('b',[_vm._v("Video")]),_c('br'),_vm._v(" Fabricato PROTECTIVE WEAR")])])])]),_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"fade-left"}},[_c('a',{staticClass:"anexo mb-4 mb-lg-0",attrs:{"href":"http://canvas.pantone.com/gallery/76156783/FABRICATO-Diseno-de-Stand","target":"_blank"}},[_c('div',{staticClass:"anexo__icono"},[_c('img',{attrs:{"src":require("@/assets/template/icono-link.svg")}})]),_c('div',{staticClass:"anexo__texto"},[_c('p',[_c('b',[_vm._v("Página web")]),_c('br'),_vm._v(" FABRICATO /Diseño de Stand ")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center align-items-center mt-5"},[_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"fade-left"}},[_c('a',{staticClass:"anexo mb-4 mb-lg-0",attrs:{"href":"https://www.youtube.com/watch?v=H0DTP58s3io","target":"_blank"}},[_c('div',{staticClass:"anexo__icono"},[_c('img',{attrs:{"src":require("@/assets/template/icono-youtube.svg")}})]),_c('div',{staticClass:"anexo__texto"},[_c('p',[_c('b',[_vm._v("Video")]),_c('br'),_vm._v(" Presentación de la colección. Marca “DANIEL ECHEVERRY” ")])])])]),_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"fade-left"}},[_c('a',{staticClass:"anex